.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(135deg, #74ebd5, #acb6e5);
  font-family: 'Arial', sans-serif;
  text-align: center;
}

.title {
  font-size: 32px;
  color: #fff;
  margin-bottom: 20px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.clock-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  width: 250px;
}

.clock-icon {
  font-size: 50px;
  color: #007bff;
  margin-right: 15px;
}

.time-display {
  font-size: 26px;
  font-weight: bold;
  color: #333;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 20px;
  margin: 10px;
  font-size: 18px;
  border: none;
  border-radius: 8px;
  background: #007bff;
  color: white;
  cursor: pointer;
  transition: background 0.3s, transform 0.2s;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
}

.btn:hover {
  background: #0056b3;
  transform: scale(1.05);
}

.btn svg {
  margin-right: 8px;
  font-size: 20px;
}
